@font-face {
  font-family: "PT Mono";
  src: url("../fonts/ptmono-regular-webfont.woff") format("woff"), url("../fonts/ptmono-regular-webfont.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed-regular-webfont.woff") format("woff"), url("../fonts/robotocondensed-regular-webfont.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed-bold-webfont.woff") format("woff"), url("../fonts/robotocondensed-bold-webfont.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  opacity: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.no-scroll {
  height: 100vh;
  overflow: hidden; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  min-width: 320px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

.container {
  max-width: 1920px;
  margin: 0 auto;
  padding-left: 375px;
  padding-right: 375px; }

.btn {
  display: block;
  min-width: 180px;
  padding-top: 13px;
  padding-bottom: 14px;
  padding-left: 13px;
  padding-right: 13px;
  font-family: "Roboto Condensed", Arial, sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: bold;
  color: #00b2ff;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.7px;
  background-color: transparent;
  border: 1px solid #00b2ff;
  border-radius: 5px; }

.contacts {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 430px; }
  @media (max-width: 1200px) {
    .contacts {
      width: 375px; } }

.contacts__link {
  display: flex;
  align-items: center;
  line-height: 18px;
  font-style: normal;
  color: #ffffff; }
  .contacts__link:hover {
    color: #00b2ff; }
  .contacts__link:active {
    color: #00b2ff;
    opacity: 0.6; }

.contacts__link-icon {
  line-height: 0; }
  .contacts__link-icon--mail {
    width: 19px;
    height: 14px;
    margin-right: 19px; }
  .contacts__link-icon--phone {
    width: 18px;
    height: 18px;
    margin-right: 17px; }

@media (max-width: 1023px) {
  .contacts {
    width: auto; }
  .contacts__link {
    font-size: 0; }
    .contacts__link--mail {
      margin-bottom: 4px;
      margin-right: 31px; }
  .contacts__link:hover .contacts__link-icon {
    opacity: 0.6; }
  .contacts__link:active .contacts__link-icon {
    opacity: 0.3; }
  .contacts__link-icon {
    margin-right: 0; }
    .contacts__link-icon--mail {
      width: 30px;
      height: 19px; }
    .contacts__link-icon--phone {
      width: 24px;
      height: 24px; } }

@media (max-width: 1023px) {
  .contacts__link--mail {
    margin-bottom: 2px;
    margin-right: 22px; } }

.page-header {
  background-color: #1b2d37; }

.page-header__container {
  display: flex;
  align-items: center;
  max-width: 1530px;
  padding-left: 60px;
  padding-right: 60px; }

.page-header__logo {
  margin-right: auto;
  line-height: 0; }

.page-header__logo picture {
  line-height: 0; }

.page-header__contacts {
  margin-right: 49px; }

.page-header__btn {
  color: #ffffff;
  border-color: #ffffff; }
  .page-header__btn:hover {
    color: #00b2ff;
    border-color: #00b2ff; }
  .page-header__btn:active {
    color: #00b2ff;
    border-color: #00b2ff;
    opacity: 0.6; }

@media (max-width: 1023px) {
  .page-header__container {
    padding-left: 24px;
    padding-right: 24px; }
  .page-header__contacts {
    margin-right: 0; }
  .page-header__btn {
    display: none; } }

@media (max-width: 767px) {
  .page-header__container {
    padding-left: 17px;
    padding-right: 15px; }
  .page-header__logo img {
    width: 38px;
    height: 38px; } }

.promo {
  min-height: 878px;
  background-image: url("../../img/bg-main-desktop@1x.jpg");
  background-size: cover;
  background-repeat: no-repeat; }
